<template>
  <div>
    <div class="logo">
      <img :src="require('@/assets/image/logo.png')" alt="" />
    </div>
    <div class="loginbody">
      <van-form @submit="submitForm">
        <img :src="require('@/assets/image/shouji.png')" alt="" class="phone" />
        <van-field
          v-model="ruleForm.phone"
          clearable
          name="用户名"
          placeholder="请输入手机号码"
          :rules="[{ required: true }]"
        />
        <img
          :src="require('@/assets/image/mima.png')"
          alt=""
          class="password"
        />
        <van-field
          v-model="ruleForm.password"
          type="password"
          clearable
          name="密码"
          placeholder="请输入密码"
          :rules="[{ required: true }]"
        />
        <div style="margin: 46px 6.6% 0px">
          <van-button color="#ff4c4c" block type="info" native-type="submit">登录</van-button
          >
        </div>
      </van-form>
    </div>
    <div class="loginbtm">
      <p @click="toforgetpsd">忘记密码</p>
      <p @click="toregister">新用户注册</p>
    </div>
    <div class="rules">
      继续即代表你同意<span @click="touserAgm">《用户协议》</span>和<span @click="toprivacy">《隐私政策》</span>
    </div>
  </div>
</template>

<script>

import * as api from '@/data/api/market/index'

export default {
  data() {
    return {
      ruleForm: {
        phone: '',
        password: '',
        code: ''
      },
      wxUrl: process.env.wxUrl
    };
  },
  mounted() {
    let me = this
    const { userAgent } = navigator
    me.isWeixin = !!/MicroMessenger/i.test(userAgent)
    // !localStorage.getItem('token') && 
    if (me.isWeixin && !me.$route.query.code) {
      let st = {
        name: 'login',
        url: '/market/home/index'
      }
      window.location.href="https://open.weixin.qq.com/connect/oauth2/authorize?appid=wx372e6d05b22ce032&redirect_uri=" + me.wxUrl + "&response_type=code&scope=snsapi_base&state=" + JSON.stringify(st) + "#wechat_redirect"
    }
  },
  methods: {
    toprivacy(){
      this.$router.push({
            path:'/user/privacyP'
          })
    },
    touserAgm(){
      this.$router.push({
        path:'/user/userAgm'
      })
    },
    // 登录
    submitForm() {
      let me = this
      if (me.$route.query.code) {
        me.ruleForm.code = me.$route.query.code
      }
      api.getSsoLogin(me.ruleForm).then(res => {
        if (res.code === 200) {
          console.log("=====>login", res.data)
          if (localStorage.getItem('token')) {
            localStorage.removeItem('token')
            localStorage.removeItem('openId')
            localStorage.setItem('openId', res.data.openId)
            localStorage.setItem('token', res.data.token)
          } else {
            localStorage.setItem('token', res.data.token)
            localStorage.setItem('openId', res.data.openId)
            localStorage.setItem('urlPath','/user/home')
          }
          if (localStorage.getItem('urlPath')) {
            if (localStorage.getItem('urlPath') === '/market/product') {
              me.$router.push({
                path: localStorage.getItem('urlPath'),
                query: {
                  id: localStorage.getItem('urlValue')
                }
              })
            } else {
              me.$router.push({
                path: localStorage.getItem('urlPath')
              })
            }
          } else {
            me.$router.push({
              path:'/market/home'
            })
          }
        } else {
          if (res.code === 1001 && res.message === 'code失效') {
            me.$toast({
              message: res.message,
              icon: 'warning-o'
            });
            let st = {
              name: 'login',
              url: '/market/home/index'
            }
            window.location.href="https://open.weixin.qq.com/connect/oauth2/authorize?appid=wx372e6d05b22ce032&redirect_uri=" + me.wxUrl + "&response_type=code&scope=snsapi_base&state=" + JSON.stringify(st) + "#wechat_redirect"
          } else {
            me.$toast({
              message: res.message,
              icon: 'warning-o'
            });
          }
        }
      }).catch((res) => {
        me.$toast({
          message: res.message,
          icon: 'warning-o'
        });
      }).finally(() => {
      })
    },
    toforgetpsd() {
      this.$router.push({
        path:'/user/forget/index'
      })
    },
    toregister() {
      this.$router.push({
        path:'/user/register/index'
      })
    }
  }
};
</script>

<style lang="stylus" scoped>
.van-button__text {
  font-size: 16px;
}
.logo {
  width: 40%;
  height: 40px;
  position: relative;
  left: 30%;
  top: 50px;
  text-align: center;

  img {
    width: 100px;
    height: 100px;
  }
}

.loginbody {
  width: 100%;
  height: 178px;
  position: relative;
  left: 0px;
  top: 130px;

  >>>.van-cell {
    padding: 15px 4.26%;
    border-bottom: 1px solid #ECECEC;
    width: 90%;
    margin-left: 5%;
  }

  >>>.van-cell::after {
    left: 4.26%;
  }

  >>>.van-field__control {
    padding-left: 15px;
  }

  .phone {
    width: 13px;
    height: 20px;
    position: absolute;
    z-index: 1;
    left: 4.8%;
    top: 18px;
  }

  .password {
    width: 17px;
    height: 19px;
    position: absolute;
    z-index: 1;
    left: 4.8%;
    top: 70px;
  }
}

.loginbtm {
  position: relative;
  left: 6.6%;
  top: 185px;
  width: 86.6%;
  height: 13px;
  display: flex;
  justify-content: space-between;
  font-size: 13px;
  color: #fd2744;
}

.rules {
  position: absolute;
  bottom: 35px;
  font-size: 12px;
  color: #888;
  width: 100%;
  text-align: center;
  span {
    color: #fd2744;
  }
}
</style>


